import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import ParallaxBanner from './parallaxBanner';


const GolvslipningHero = () => {

    const { gcms } = useStaticQuery(graphql`
    {
        gcms {
            golvslipnings {
                hero {
                  url
                }
              }
            }
    }

  `)

    return (
        <ParallaxBanner url={gcms.golvslipnings[0].hero.url} heading="Dammfri Golvslipning"/>

    )
}

export default GolvslipningHero;